.auth-form__button {
    width: 100%;
    height: 50px;
    margin: 218px 0 0;
    border: none;
    padding: 0;
    font-size: 18px;
    border-radius: 2px;
    opacity: 100%;
    transition: .3s, ease-out;
}

.auth-form__button:hover {
    opacity: 85%;
    transition: .3s, ease-out;
}